body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* HOMEPAGE/component */
.container-full-width {
  height: 100vm;
  padding: 5em;
}

.navbar {
  background-color: none;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.navbar button {
  margin-left: 1em;
}

.login-container input {
  text-align: center;
}

.header {
  padding: 20px;
  /* background-color:#66bb6a; */
  height: 45em;
  text-align: center;
}

.section {
  height: 100vh;
  padding: 20px;
  overflow: auto;
  text-align: center;
}

.boxShadow {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.headerbox {
  margin: 0 auto;
  width: 100%;
  height: 100%;
}

.box-home {
  width: 100%;
  height: 100%;
}

.box-home img {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.section img {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.section p {
  text-align: justify;
}

#parent {
  display: table;
  text-align: center;
}

#child {
  display: table-cell;
  vertical-align: middle;
}

.row img {
  border-radius: 5px;
}

.signup-container {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
  height: 100%;
}

.signup-container input {
  text-align: center;
  width: 100%;
  margin-bottom: 1em;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}

.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

/* sign up and login form */
.forms {
  width: 400px;
  background-color: #FAFAFA;
  padding: 1em;
  margin: 1em;
  border-radius: 5px;
}

/* PROFILE PAGE CARD */
.profile-page-container {
  background-color: #6d4c41;
  background-image: url(https://i.pinimg.com/originals/e5/8a/e5/e58ae5ca3f368c6e490602a511c05ed2.jpg);
  background-size: cover;
}

.card-notes {
  background-image: url(https://www.bromsgroveadvertiser.co.uk/resources/static/shaken/images/bg-texture2.jpg);
}

.card-content-list {
  height: 10vh;
  overflow: scroll;
}

.card header {
  background-color: #6d4c41;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto;
}

.grid-item {
  text-align: left;
  margin-right: 10px;
}

.card-display-panel {
  min-height: 80vh;
}


/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) { 
  #dropdown-list {
    text-align: center;
  }
}

@media (max-width: 576px) { 
  .grid-container {
    grid-template-columns: auto;
  }
}


